/* Type */

body,
input,
select,
textarea {
	color: _palette(fg);
	font-family: _font(family);
	font-size: 14pt;
	font-weight: _font(weight);
	line-height: 1.75;

	@include breakpoint(xlarge) {
		font-size: 12pt;
	}

	@include breakpoint(large) {
		font-size: 12pt;
	}

	@include breakpoint(medium) {
		font-size: 12pt;
	}

	@include breakpoint(small) {
		font-size: 12pt;
	}

	@include breakpoint(xsmall) {
		font-size: 12pt;
	}
}

a {
	@include vendor('transition', ('color #{_duration(transition)} ease',
			'border-bottom-color #{_duration(transition)} ease'
		));
	border-bottom: dotted 1px _palette(border-alt);
	color: inherit;
	text-decoration: none;

	&:before {
		@include vendor('transition', ('color #{_duration(transition)} ease'
			));
	}

	&:hover {
		border-bottom-color: transparent;
		color: _palette(accent) !important;

		&:before {
			color: _palette(accent) !important;
		}
	}
}

strong,
b {
	color: _palette(fg-bold);
	font-weight: _font(weight-bold);
}

em,
i {
	font-style: italic;
}

p {
	margin: 0 0 _size(element-margin) 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: _palette(fg-bold);
	font-family: _font(family-heading);
	font-weight: _font(weight-heading-bold);
	letter-spacing: _font(kerning-heading);
	line-height: 1.65;
	margin: 0 0 (_size(element-margin) * 0.5) 0;
	text-transform: uppercase;

	a {
		color: inherit;
		border-bottom: 0;
	}
}

h2 {
	font-size: 1.1em;
}

h3 {
	font-size: 0.9em;
}

h4 {
	font-size: 0.7em;
}

h5 {
	font-size: 0.7em;
}

h6 {
	font-size: 0.7em;
}

sub {
	font-size: 0.8em;
	position: relative;
	top: 0.5em;
}

sup {
	font-size: 0.8em;
	position: relative;
	top: -0.5em;
}

blockquote {
	border-left: solid 4px _palette(border);
	font-style: italic;
	margin: 0 0 _size(element-margin) 0;
	padding: calc(_size(element-margin) / 4) 0 calc(_size(element-margin) / 4) _size(element-margin);
}

code {
	background: _palette(border-bg);
	border: solid 1px _palette(border);
	font-family: _font(family-fixed);
	font-size: 0.9em;
	margin: 0 0.25em;
	padding: 0.25em 0.65em;
}

pre {
	-webkit-overflow-scrolling: touch;
	font-family: _font(family-fixed);
	font-size: 0.9em;
	margin: 0 0 _size(element-margin) 0;

	code {
		display: block;
		line-height: 1.75em;
		padding: 1em 1.5em;
		overflow-x: auto;
	}
}

hr {
	border: 0;
	border-bottom: solid 1px _palette(border);
	margin: _size(element-margin) 0;

	&.major {
		margin: (_size(element-margin) * 1.5) 0;
	}
}

.align-left {
	text-align: left;
}

.align-center {
	text-align: center;
}

.align-right {
	text-align: right;
}